.menu {
     position: fixed;
     top: 0;
     background-color: #292929;
     width: 100vw;
     height: 100vh;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     transition: 0.4s;
     transform: translateX(-100vw);
     z-index: 9;
}
.menu-show {
     transform: translateX(0vw);
}
.menu-logo {
     position: fixed;
     width: 180px;
     top: 15px;
     right: 10px;
     z-index: 200;
}
.menu li {
     color: #fff;
}

.hamburger-container {
     background-color: #292929;
     width: 100vw;
     height: 60px;
     position: fixed;
     top: 0px;
     left: 0px;
     z-index: 10;
}

.hamburger {
     position: fixed;
     left: 10px;
     top: 20px;
     z-index: 10;
}

.ham-line {
     width: 30px;
     height: 3px;
     background-color: #fff;
     margin-bottom: 4px;
     border-radius: 20px;
     transition: 0.4s;
     transform-origin: center;
}

.ham-line_one {
     transform: rotate(45deg) translate(5px, 5px);
     transform-origin: center;
}
.ham-line_two {
     opacity: 0;
}
.ham-line_three {
     transform: rotate(-45deg) translate(5px, -5px);
}

@media screen and (min-width: 800px) {
     .hamburger-container {
          display: none;
     }
     .menu {
          width: 100vw;
          height: 90px;
          margin: 0;
          position: fixed;
          background-color: #292929;
          flex-direction: row;
          justify-content: flex-end;
          align-items: center;
          padding-right: 5vw;
          transform: translateX(0px);
     }
     .menu-logo {
          width: 250px;
          top: 23px;
          left: 2vw;
     }
     .menu li {
          margin-left: 5%;
     }
}

.input-search-container {
     position: relative;
}

.input-search-menu {
     height: 44px;
     width: 291px;
     border-radius: 26.5px;
     border: none;
     font-size: 12px;
     font-family: inherit;
     padding-left: 135px;
     cursor: pointer;
     transition: all 0.2s ease-in-out;
}

.input-search-menu:focus {
     outline: 1px solid rgb(69, 181, 255);
}

.input-search-menu:hover {
     background: #efefef;
}

.input-search-menu::placeholder {
     color: #00000080;
}

.menu-lupa {
     position: absolute;
     top: 50%;
     left: 25px;
     transform: translate(0, -50%);
}

.inner-select {
     position: absolute;
     top: 0;
}

.inner-select-label {
     height: 44px;
     display: flex;
     align-items: center;
     padding-left: 50px;
}

.border-select {
     display: flex;
     width: 73px;
     height: 70%;
     align-items: center;
     gap: 4px;
     cursor: pointer;
     border-right: 1px solid #000000;
}

.search-selector {
     font-size: 12px;
     font-family: "Ubuntu";
     color: rgba(0, 0, 0, 0.671);
}

.arrow-down-select {
     margin-right: 9px;
     width: 12px;
}

.list-search-selector {
     position: relative;
     top: -3px;
     left: 43px;
     padding-left: 7px;
     font-size: 12px;
     color: #00000080;
     background-color: #fff;
     padding-bottom: 8px;
     display: block;
     z-index: 50;
     box-shadow: 0 5px 6px rgba(0, 0, 0, 0.185);
}

.hidden-selector {
     display: none;
}

.list-option {
     margin: 3px 0;
     cursor: pointer;
}

.search-results {
     position: absolute;
     top: 50px;
     width: 291px;
     height: 180px;
     background: #fff;
     color: #000000;
     overflow-y: auto;
     border-radius: 10px;
     box-shadow: 0 0 14px rgba(0, 0, 0, 0.308);
}

.item-result {
     display: flex;
     align-items: center;
     gap: 4px;
     margin-bottom: 8px;
     cursor: pointer;
}

.item-result-img {
     width: 60px;
}

.book-result-name {
     font-family: "Ubuntu";
     font-size: 12px;
}

.hidden-results {
     display: none;
}

.search-message {
     text-align: center;
     font-family: "Ubuntu";
}