body {
     margin: 0;
     font-family: "Lato", sans-serif;
     -webkit-font-smoothing: antialiased;
     -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3 {
     font-family: "Ubuntu", sans-serif;
}

.App {
     text-align: center;
}

.App-logo {
     height: 40vmin;
     pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
     .App-logo {
          animation: App-logo-spin infinite 20s linear;
     }
}

.App-header {
     background-color: #282c34;
     min-height: 100vh;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     font-size: calc(10px + 2vmin);
     color: white;
}

.App-link {
     color: #61dafb;
}

@keyframes App-logo-spin {
     from {
          transform: rotate(0deg);
     }
     to {
          transform: rotate(360deg);
     }
}

.buy-book-btn {
     height: 30px;
     width: 101px;
     border: none;
     border-radius: 15px;
     background: #fe8f3c;
     font-weight: 500;
     font-family: inherit;
     color: #fff;
     cursor: pointer;
}

.book-card-text {
     display: flex;
     flex-direction: column;
     justify-content: flex-start;
}

.btn-book-container {
  display: flex;
  align-items: flex-end;
}

.btn-book-container:after {
     padding-bottom: 100%;
}
